import { Injectable, signal, computed, effect, inject } from '@angular/core';
import { Note, SortOrder } from '../models/note.model';
import { StorageService } from './storage.service';

@Injectable({
  providedIn: 'root'
})
export class NoteService {
  private storageService = inject(StorageService);

  // State
  notes = signal<Note[]>([]);
  categories = signal<string[]>(['Ï†ÑÏ≤¥', 'ÏóÖÎ¨¥', 'ÏÉùÌôú', 'ÏïÑÏù¥ÎîîÏñ¥', 'Ïó¨Ìñâ']);
  currentCategory = signal('Ï†ÑÏ≤¥');
  searchQuery = signal('');
  sortOrder = signal<SortOrder>('date');

  // Computed
  filteredNotes = computed(() => {
    let list = this.notes().filter(n => {
      const catMatch = this.currentCategory() === 'Ï†ÑÏ≤¥' || n.category === this.currentCategory();
      const searchMatch = n.title.toLowerCase().includes(this.searchQuery().toLowerCase()) ||
        n.content.toLowerCase().includes(this.searchQuery().toLowerCase());
      return catMatch && searchMatch;
    });

    // Sort
    if (this.sortOrder() === 'date') {
      list.sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
    } else {
      list.sort((a, b) => a.title.localeCompare(b.title));
    }

    // Pinned on top
    return list.sort((a, b) => (a.isPinned === b.isPinned ? 0 : a.isPinned ? -1 : 1));
  });

  pinnedNotes = computed(() => this.filteredNotes().filter(n => n.isPinned));
  otherNotes = computed(() => this.filteredNotes().filter(n => !n.isPinned));

  constructor() {
    // Load from storage
    this.loadData();

    // Auto-save on changes
    effect(() => {
      this.storageService.saveNotes(this.notes());
      this.storageService.saveCategories(this.categories());
    });
  }

  private loadData(): void {
    const savedNotes = this.storageService.loadNotes();
    if (savedNotes.length > 0) {
      this.notes.set(savedNotes);
    } else {
      this.initializeSampleData();
    }

    const savedCategories = this.storageService.loadCategories();
    if (savedCategories.length > 0) {
      this.categories.set(savedCategories);
    }
  }

  private initializeSampleData(): void {
    this.notes.set([
      {
        id: 1,
        title: 'üìå ÌîÑÎ°úÏ†ùÌä∏ Í∏∞ÌöçÏïà',
        content: 'Easy Note Ïï± Í∏∞ÌöçÏÑú Î¶¨Î∑∞ Î∞è Í∏∞Îä• Ï†ïÏùò.\nAI ÏûêÎèô Î∂ÑÎ•ò Í∏∞Îä•Ïù¥ ÌïµÏã¨.',
        category: 'ÏóÖÎ¨¥',
        isPinned: true,
        date: new Date().toISOString(),
        images: [],
        hasAudio: true
      },
      {
        id: 2,
        title: 'Ï£ºÎßê Ïû•Î≥¥Í∏∞ Î™©Î°ù',
        content: '- Ïö∞Ïú†\n- Í≥ÑÎûÄ\n- ÏÇ¨Í≥º\n- ÌååÏä§ÌÉÄ Î©¥\n- ÌÜ†ÎßàÌÜ† ÏÜåÏä§',
        category: 'ÏÉùÌôú',
        isPinned: false,
        date: new Date(Date.now() - 86400000).toISOString(),
        images: [],
        hasAudio: false
      }
    ]);
  }

  // CRUD Operations
  createNote(category?: string): Note {
    const newNote: Note = {
      id: Date.now(),
      title: '',
      content: '',
      category: category || (this.currentCategory() === 'Ï†ÑÏ≤¥' ? 'ÏóÖÎ¨¥' : this.currentCategory()),
      isPinned: false,
      date: new Date().toISOString(),
      images: [],
      hasAudio: false
    };
    this.notes.update(notes => [newNote, ...notes]);
    return newNote;
  }

  updateNote(id: number, changes: Partial<Note>): void {
    this.notes.update(notes =>
      notes.map(n => n.id === id ? { ...n, ...changes, date: new Date().toISOString() } : n)
    );
  }

  deleteNote(id: number): void {
    this.notes.update(notes => notes.filter(n => n.id !== id));
  }

  togglePin(id: number): void {
    this.notes.update(notes =>
      notes.map(n => n.id === id ? { ...n, isPinned: !n.isPinned } : n)
    );
  }

  duplicateNote(id: number): Note | undefined {
    const original = this.notes().find(n => n.id === id);
    if (original) {
      const copy: Note = {
        ...original,
        id: Date.now(),
        title: original.title + ' (Î≥µÏÇ¨Î≥∏)',
        date: new Date().toISOString()
      };
      this.notes.update(notes => [copy, ...notes]);
      return copy;
    }
    return undefined;
  }

  getNoteById(id: number): Note | undefined {
    return this.notes().find(n => n.id === id);
  }

  // Category Operations
  addCategory(name: string): boolean {
    const trimmed = name.trim();
    if (trimmed && !this.categories().includes(trimmed)) {
      this.categories.update(cats => [...cats, trimmed]);
      return true;
    }
    return false;
  }

  deleteCategory(name: string): void {
    this.categories.update(cats => cats.filter(c => c !== name));
    this.notes.update(notes => notes.filter(n => n.category !== name));
    if (this.currentCategory() === name) {
      this.currentCategory.set('Ï†ÑÏ≤¥');
    }
  }

  getCategoryCount(category: string): number {
    if (category === 'Ï†ÑÏ≤¥') return this.notes().length;
    return this.notes().filter(n => n.category === category).length;
  }
}
