import { Injectable, signal } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class AudioService {
  isRecording = signal(false);
  isPlaying = signal(false);
  progress = signal(0);
  currentTime = signal(0);
  totalTime = signal(180); // 3Î∂Ñ
  
  private intervalId: any;

  startRecording(): Promise<void> {
    return new Promise((resolve) => {
      this.isRecording.set(true);
      // Simulate recording for 2 seconds
      setTimeout(() => {
        this.stopRecording();
        resolve();
      }, 2000);
    });
  }

  stopRecording(): void {
    this.isRecording.set(false);
  }

  play(): void {
    this.isPlaying.set(true);
    this.intervalId = setInterval(() => {
      const current = this.currentTime();
      const total = this.totalTime();
      
      if (current >= total) {
        this.stop();
        return;
      }
      
      this.currentTime.update(t => t + 1);
      this.progress.set((this.currentTime() / total) * 100);
    }, 1000);
  }

  pause(): void {
    this.isPlaying.set(false);
    if (this.intervalId) clearInterval(this.intervalId);
  }

  stop(): void {
    this.isPlaying.set(false);
    this.currentTime.set(0);
    this.progress.set(0);
    if (this.intervalId) clearInterval(this.intervalId);
  }

  skip(seconds: number): void {
    let newTime = this.currentTime() + seconds;
    if (newTime < 0) newTime = 0;
    if (newTime > this.totalTime()) newTime = this.totalTime();
    this.currentTime.set(newTime);
    this.progress.set((newTime / this.totalTime()) * 100);
  }

  formatTime(seconds: number): string {
    const m = Math.floor(seconds / 60).toString().padStart(2, '0');
    const s = (seconds % 60).toString().padStart(2, '0');
    return `${m}:${s}`;
  }

  reset(): void {
    this.stop();
    this.totalTime.set(180);
  }
}
